import { Box, Card, Lead } from 'rebass'
import DeleteTodo from './DeleteTodo'
import UpdateTodo from './UpdateTodo'

class Todo extends React.Component {
  render() {
    return (
      <Box>
        <Card
          p={2}
          borderRadius={2}
          boxShadow="0 0 16px rgba(0, 0, 0, .25)"
          mt={[2, 3, 4]}
        >
          {/* If clicked, display update todo */}
          <Lead fontSize={[2, 3, 4]}>{props.todo.task}</Lead>
          <UpdateTodo />
          <DeleteTodo id={props.todo.id} />
        </Card>
      </Box>
    )
  }
}

export default Todo
